import React from 'react';
import Media from 'react-media';
import style from './information.module.css';
import Buttons from '../../buttons/buttons-dark';
import img from '../../../img/infoImg/callCenter.png';
import imgW from '../../../img/infoImg/callCenter.webp';

const Information = ({ onOpenModal }) => (
  <div className={style.wrapper}>
    <p className={style.text}>
      Управление заявками абонентов, контроль исполнения, архив обращений и
      отчеты эффективности. А так же корпоративная АТС с функциями голосового
      меню и записью переговоров
    </p>
    <Media query={{ maxWidth: 1199 }}>
      {matches =>
        matches && (
          <picture>
            <source srcSet={imgW} type="image/webp" />
            <source srcSet={img} type="image/png" />
            <img className={style.img} src={img} alt="CallCenter" />
          </picture>
        )
      }
    </Media>
    <Buttons
      path="callCenter"
      onOpenModal={onOpenModal}
      className={style.buttons}
    />
  </div>
);

export default Information;
