import React, { Fragment } from 'react';
import Media from 'react-media';
import style from './seventh-screen.module.css';
import Header from './header/header';
import NavigationServices from '../navigationServices/navigationServices';
import Information from './infornation/information';
import callCenterImg from '../../img/infoImg/callCenter.png';
import callCenterImgW from '../../img/infoImg/callCenter.webp';
import seventhScreenSomethinImg from '../../img/background/seventhScreenSomethingDesk.png';
import seventhScreenSomethinImgW from '../../img/background/seventhScreenSomethingDesk.webp';
import seventhScreenSomethin2Img from '../../img/background/seventhScreenSomethingDesk2.png';
import seventhScreenSomethin2ImgW from '../../img/background/seventhScreenSomethingDesk2.webp';

const SeventhSection = ({ onOpenModal }) => (
  <section id="CallCenter" name="CallCenter" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={6} />
          </div>
        )
      }
    </Media>
    <Information onOpenModal={onOpenModal} />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <Fragment>
            <picture>
              <source srcSet={callCenterImgW} type="image/webp" />
              <source srcSet={callCenterImg} type="image/png" />
              <img className={style.img} src={callCenterImg} alt="CallCenter" />
            </picture>
            <picture>
              <source srcSet={seventhScreenSomethinImgW} type="image/webp" />
              <source srcSet={seventhScreenSomethinImg} type="image/png" />
              <img
                className={style.something1}
                src={seventhScreenSomethinImg}
                alt="back1"
              />
            </picture>
            <picture>
              <source srcSet={seventhScreenSomethin2ImgW} type="image/webp" />
              <source srcSet={seventhScreenSomethin2Img} type="image/png" />
              <img
                className={style.something2}
                src={seventhScreenSomethin2Img}
                alt="back2"
              />
            </picture>
          </Fragment>
        )
      }
    </Media>
  </section>
);

export default SeventhSection;
